import React, { useEffect } from 'react';
import { createPortal } from 'react-dom';
import { RemoveScroll } from 'react-remove-scroll';

import { Overlay } from './Modal.styled';
import ModalAddMyPet from './ModalAddMyPet/ModalAddMyPet';
import ModalInfo from './ModalInfo/ModalInfo';
import ModalUser from './ModalUser/ModalUser';
import ModalNotice from './ModalNotice/ModalNotice';

const modalRoot = document.querySelector('#modal-root');

const Modal = ({ children, onClose, title, type }) => {
  console.log(type);
  useEffect(() => {
    function keyDown(e) {
      if (e.code !== 'Escape') {
        return;
      }
      onClose();
    }
    window.addEventListener('keydown', keyDown);
    return () => {
      window.removeEventListener('keydown', keyDown);
    };
  }, [onClose]);

  function handleOverlayClick(e) {
    if (e.target === e.currentTarget) {
      onClose();
    }
    return;
  }

  return createPortal(
    <RemoveScroll>
      <Overlay onClick={handleOverlayClick}>
        {type === 'info' && <ModalInfo onClose={onClose}>{children}</ModalInfo>}
        {type === 'user' && (
          <ModalUser onClose={onClose} title={title}>
            {children}
          </ModalUser>
        )}
        {type === 'pet' && (
          <ModalAddMyPet onClose={onClose} title={title}>
            {children}
          </ModalAddMyPet>
        )}
        {type === 'notice' && (
          <ModalNotice onClose={onClose} title={title}>
            {children}
          </ModalNotice>
        )}
      </Overlay>
    </RemoveScroll>,
    modalRoot
  );
};

export default Modal;
